body {
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  padding: 20px;
}

/* Add styles for the careers page */
.careers-page {
  max-width: 800px;
  margin: 0 auto;
}

/* Style for each job listing */
.job-listing {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 20px 0;
  background-color: #f9f9f9;
}

/* Style for the apply button */
.apply-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
}